.calContainer{
    width:300px;
    height:300px;
    border:1px solid black;
    margin:30px;
    margin-left: 40%;
    margin-top: 140px;
    border-radius: 10px;
    background-color: aliceblue;
}

.calContainer > h3{
    text-align: center;
}

input{
    margin-left:40px;
    margin-bottom: 20px;
    height:20px;
    width:200px;
}

.calcbtn{
    display: flex;
    margin-left: 45px;
    gap: 10px;
    margin-top: 5px;
}

.calcbtn > button{
    width:40px;
    height:30px;
    margin-left: 25px;
    background-color: black;
    color: white;
    border: none;
    margin-left: 2px;

    
}

.successmsg{
    text-align: center;
    color:green;
}

.errormsg{
    text-align:center;
    color:red;
}

.resultmsg{
    text-align: center;
    font-weight: 400;
}